
import Vue from 'vue'

export default Vue.extend({
  name: 'skills-section',

  data () {
    return {
      skills: [
        'C#',
        '.NET',
        'Javascript',
        'CSS',
        'Ruby',
        'RESTful',
        'Linux Systems',
        'HTML',
        'Vue',
        'Sencha',
        'SQL',
        'ASP.NET MVC',
        'Azure DevOps',
        'Java',
        'Python',
        'Git',
        'jQuery',
        'Redmine',
        'TypeScript'
      ].sort(function (a, b) {
        return a.toLowerCase().localeCompare(b.toLowerCase())
      })
    }
  }
})
